* {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: 'Montserrat';
  color: aliceblue;
  list-style: none;
  text-decoration: none;
  outline: none;
}
body {
  margin: 0;
  background: url("../images/crater.jpg");
  background-attachment: fixed;
  background-size: 100%;
  position: relative;
  overflow-x: hidden;
}
body:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, transparent, rgba(0, 255, 246, 0.7), transparent);
  animation-fill-mode: forwards;
  display: block;
  z-index: -1;
}
@keyframes changeFilter {
  from {
    background: rgba(0, 255, 246, 0.7);
  }
  to {
    background: rgba(0, 255, 246, 0.3);
  }
}
@media screen and (max-width: 604px) {
  body {
    background: url("../images/textura-amarilla.png");
  }
  body:before {
    background: linear-gradient(to right, transparent, rgba(0, 255, 246, 0.7));
  }
}
.Card {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin: 15px 5px;
  padding: 0 15px;
  background: linear-gradient(to right, rgba(240, 248, 255, 0.3), transparent, transparent, transparent, transparent);
  box-shadow: 5px 5px 15px 1px black;
  border-radius: 30px;
  text-shadow: 1.5px 1.5px 1px black;
  cursor: pointer;
  height: 300px;
  width: auto;
}
.Card .ImageContent {
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../images/crater.jpg");
  background-attachment: fixed;
  background-size: 100%;
  border-radius: 50%;
  border: 2px solid #55345b;
}
.Card .ImageContent .CardImage {
  width: 150px;
  height: auto;
}
.Card .CardContent {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  border-radius: 0 0 30px 30px;
}
.Card .CardContent .CardText {
  font-size: 15px;
  font-family: 'Montserrat';
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 20px 0;
  text-align: center;
}
@media screen and (max-width: 801px) {
  .Card {
    flex-direction: initial;
    padding: 30px;
    height: auto;
    width: auto;
    background: none;
    box-shadow: none;
    width: 100%;
  }
  .Card .ImageContent {
    background: none;
    border: none;
    box-shadow: 2px 2px 2px 0px black;
    width: 150px;
    height: 150px;
  }
  .Card .ImageContent .CardImage {
    width: 150px;
    height: auto;
  }
  .Card .CardContent .CardText {
    font-size: 1.5em;
  }
}
@media screen and (max-width: 604px) {
  .Card {
    width: 100%;
    flex-direction: initial;
  }
  .Card .ImageContent {
    background: none;
    border: none;
    box-shadow: 2px 2px 2px 0px black;
    width: 150px;
    height: 150px;
  }
  .Card .ImageContent .CardImage {
    width: 150px;
    height: auto;
  }
  .Card .CardContent .CardText {
    font-size: 1.5em;
  }
}
.Modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: Modal 0.4s;
}
.Modal .ModalContent {
  border: 2px solid #65ffd4;
  border-radius: 5px;
  max-height: 80%;
  overflow-y: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  padding: 7px;
  min-width: 25vw;
}
.Modal .ModalContent .ModalTitle {
  width: 100%;
  border-radius: 5px;
  background: black;
  color: #65ffd4;
  font-family: 'Montserrat' !important;
  font-weight: bold;
  text-align: center;
  padding: 10px 0;
}
.Modal .ModalContent .ModalTitle:before {
  font-family: "icomoon" !important;
  padding: 0 2px 3px;
  content: "\e9d9";
  color: #efe94e !important;
}
.Modal .ModalContent .ModalChildren {
  text-align: center;
  margin: 10px 0;
  padding: 15px;
  background: rgba(0, 0, 0, 0.5);
}
.Modal .ModalContent .ModalClose {
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 50px;
  font-weight: 100;
  border: none;
  margin: 0;
  padding: 0;
  padding: 15px;
  font-weight: bolder;
  border-radius: 5px;
  box-shadow: 5px 5px 15px 1px black;
  transition: 0.2s;
  cursor: pointer;
  background: transparent;
  color: #55345b;
  box-shadow: none;
}
.Modal .ModalContent .ModalClose:before {
  font-family: "icomoon" !important;
  padding: 0 2px 3px;
  content: "\ea0d";
  color: #65ffd4 !important;
}
@keyframes changeColor {
  0% {
    background: transparent;
  }
  100% {
    background: #9d63c9;
  }
}
.Modal .ModalContent .ModalClose:active {
  transform: scale(0.95);
}
@keyframes Modal {
  from {
    filter: blur(15px);
  }
  to {
    filter: blur(0px);
  }
}
@media screen and (max-width: 604px) {
  .Modal .ModalContent .ModalTitle {
    font-size: 0.95em;
    font-weight: 500;
  }
  .Modal .ModalContent .ModalClose {
    position: absolute;
    top: 7px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
    font-size: 25px;
    font-weight: 100;
    margin: 0;
    padding: 0;
    font-size: 2em;
  }
  .Modal .ModalContent .ModalClose:before {
    font-family: "icomoon" !important;
    padding: 0 2px 3px;
    content: "\ea0d";
    color: #65ffd4 !important;
  }
}
.Menu {
  background: rgba(85, 52, 91, 0.3);
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Menu .titleMenu {
  color: transparent;
  height: 100%;
  padding: 0px 25px 0px 0px;
  border-radius: 0 30px 30px 0;
  border-left: 0;
  display: inline;
  background: black;
  cursor: pointer;
  font-size: 22px;
  position: relative;
  font-family: 'Inconsolata';
}
.Menu .titleMenu:before {
  content: attr(data-text);
  position: absolute;
  top: 7px;
  left: 7px   ;
  right: 0;
  bottom: 0;
  height: 65%;
  width: 86.5%;
  padding: 0;
  color: aliceblue;
  overflow: hidden;
  border: 2px solid aliceblue;
  border-left: none;
  border-top: none;
  border-bottom: none;
  animation: type 5s steps(13) infinite, blinkTextCursor 500ms steps(13) infinite;
  white-space: nowrap;
  transition: 0.3s;
}
.Menu .titleMenu:hover::before {
  color: #65ffd4;
}
.Menu .MenuClose {
  display: none;
}
.MenuElements {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 80%;
  height: 100%;
}
.nav-item {
  height: 100%;
  width: 16vw;
}
.nav-item a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: 0.5s;
  font-family: 'Montserrat' !important;
  border: 2px solid transparent;
  border-top: 0;
  border-bottom: 0;
}
.nav-item a:before {
  font-family: "icomoon" !important;
  padding: 0 2px 3px;
  content: "\e9d9";
  color: #efe94e !important;
}
.nav-item a:hover {
  background: rgba(0, 0, 0, 0.7);
  border-top: 0;
  border-bottom: 0;
  color: #65ffd4;
}
.nav-item a:hover::before {
  color: #efe94e !important;
}
@keyframes type {
  0% {
    width: 0;
  }
  25% {
    width: 87%;
  }
  75% {
    width: 87%;
  }
  100% {
    width: 0;
  }
}
@keyframes blinkTextCursor {
  0% {
    border-right-color: aliceblue;
  }
  100% {
    border-right-color: transparent;
  }
}
@media screen and (max-width: 769px) {
  .Menu {
    height: 75px;
    background: none;
  }
  .Menu .MenuElementsClose {
    left: -100vw !important;
  }
  .Menu .titleMenu {
    height: 55%;
    font-size: 25px;
    padding-right: 30px;
  }
  .Menu .MenuElements {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    transition: 0.2s;
    z-index: 4;
    background: rgba(0, 0, 0, 0.7);
  }
  .Menu .MenuElements * {
    color: #55345b;
    font-weight: bolder;
    font-size: 1.1em;
  }
  .Menu .MenuElements .nav-item {
    width: 100%;
    height: auto;
    margin: 35px 0;
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .Menu .MenuElements .nav-item a {
    height: auto;
    padding: 15px 0;
    border-top: 0;
    border-bottom: 0;
    color: #65ffd4;
  }
  .Menu .MenuClose {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 6;
    background: transparent;
    font-size: 45px;
    top: 7px;
    right: 7px;
    cursor: pointer;
  }
  .Menu .MenuClose:before {
    font-family: "icomoon" !important;
    padding: 0 2px 3px;
    content: "\ea0d";
    color: #efe94e !important;
  }
  .Menu .MenuOpen {
    display: flex;
    justify-content: center;
    align-items: center;
    position: static;
    z-index: 6;
    background: black;
    border-radius: 30px 0 0 30px;
    font-size: 25px;
    padding: 0px 10px 0 15px;
    height: 55%;
    top: 7px;
    right: 7px;
    cursor: pointer;
  }
  .Menu .MenuOpen:before {
    font-family: "icomoon" !important;
    padding: 0 2px 3px;
    content: "\e9bd";
    color: aliceblue !important;
  }
}
.About {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 3fr 1fr 1fr;
  width: 100%;
  margin: 20px 0 ;
  padding: 10px 0;
  opacity: 0.9;
}
.About .AboutImg {
  grid-column: 3 / 4;
  border-radius: 0 30px 30px 0;
  box-shadow: 10px 10px 5px 1px black;
  width: 200px;
  height: auto;
}
.About .AboutText {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: end;
  padding: 0 15px;
  grid-column: 2 /3;
  background: url("../images/textura-violeta.png");
  background-size: 18vw;
  border-radius: 30px 0 0 30px;
  box-shadow: 10px 10px 5px 1px black;
}
@media screen and (max-width: 801px) {
  .About {
    grid-template-columns: 1fr 6fr 2fr 1fr;
  }
  .About .AboutText {
    font-size: 20px;
  }
}
@media screen and (max-width: 769px) {
  .About {
    grid-template-columns: 1fr 6fr 2fr 1fr;
  }
  .About .AboutText {
    font-size: 17px;
  }
}
@media screen and (max-width: 604px) {
  .About {
    grid-template-columns: 1fr 8fr 1fr;
    position: relative;
  }
  .About .AboutImg {
    grid-row: 1/2;
    grid-column: 2/3;
    border-radius: 50%;
    box-shadow: none;
    position: absolute;
    width: 75px;
    height: 75px;
    bottom: -15%;
    right: -5%;
    border: 2px solid #efe94e;
  }
  .About .AboutText {
    grid-row: 1/2;
    grid-column: 2/3;
    background: #55345b;
    border-radius: 5px;
    box-shadow: 5px 5px 15px 1px black;
    height: 150px;
    text-align: start;
  }
}
@media screen and (max-width: 415px) {
  .About .AboutText {
    font-size: 15px;
  }
}
.Services {
  display: grid;
  width: 100%;
  justify-content: center;
  align-items: center;
  justify-items: center;
  align-content: center;
  grid-template-columns: 1fr 4fr 1fr;
  grid-template-rows: 1fr 8fr 1fr;
}
.Services .ServicesTitle {
  grid-column: 1/4;
  grid-row: 1/2;
  width: 100%;
  text-align: center;
  padding: 10px 0;
  background: linear-gradient(to right, rgba(240, 248, 255, 0.3), transparent, transparent, transparent, transparent);
  box-shadow: 5px 5px 15px 1px black;
  border-radius: 30px;
  text-shadow: 1.5px 1.5px 1px black;
  border-radius: 0;
}
.Services .ServicesElement {
  width: 100%;
  grid-column: 2/3;
  grid-row: 2/3;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.Services .ServicesButton {
  grid-column: 2/3;
  grid-row: 3/4;
  border: none;
  margin: 0;
  padding: 0;
  padding: 15px;
  font-weight: bolder;
  border-radius: 5px;
  box-shadow: 5px 5px 15px 1px black;
  transition: 0.2s;
  cursor: pointer;
  background: #9d63c9;
  color: aliceblue;
}
@keyframes changeColor {
  0% {
    background: transparent;
  }
  100% {
    background: #9d63c9;
  }
}
.Services .ServicesButton:active {
  transform: scale(0.95);
}
@media screen and (max-width: 801px) {
  .Services {
    grid-template-columns: 1fr 10fr 1fr;
  }
  .Services .ServicesElement {
